.Repo {
  position: absolute;
  top: calc(var(--header-height) + 1px);
  bottom: 0;
  left: 0;
  right: 0;
}

.CellMetaData {
  position: absolute;
  top: 0;
  right: 0;
  width: 300px;
  border: 2px solid white;
  border-radius: 8px;
  margin: 15px;
  padding: 8px;
}

.CellMetaData p {
  color: white;
  margin: 5px;
  font-family: "SF Mono";
}

#focusedLabel {
  font-weight: bold;
  font-size: larger;
  margin-bottom: 15px;
  padding-bottom: 5px;
  border-bottom: 1px dashed white;
}

.PowerDiagram {
  width: 100%;
  height: 100%;
}

.Polygon polygon {
  fill: var(--background-color);
  fill-opacity: 1;
  stroke: white;
}

.Polygon text {
  fill: white;
  pointer-events: none;
}

.noOverlay.Polygon.dirPolygon.hasNoLabel.relativeTier1 polygon {
  fill: white;
  fill-opacity: 0.15 !important;
}

.noOverlay.Polygon:hover polygon, .noOverlay.Polygon.dirPolygon.hasNoLabel:hover polygon {
  fill: white;
  fill-opacity: 0.4 !important;
}

.heatmapOverlay.Polygon.dirPolygon polygon {
  fill-opacity: 0 !important;
  pointer-events: none;
}

.heatmapOverlay.Polygon.filePolygon:hover polygon {
  fill: white;
  fill-opacity: 0.4 !important;
}

.OverlayBar {
  background: transparent;
  position: absolute;
  top: 0;
  left: 0;
  margin-left: 15px;
  margin-top: 15px;
}

.OverlayButton {
  border-width: 2px;
  border-style: solid;
  border-radius: 8px;
  padding: 5px;
  cursor: pointer;
  text-align: center;
  font-size: 20px;
}

.OverlayButton + .OverlayButton {
  margin-top: 5px;
}

.OverlayButton.inactive {
  color: grey;
  border-color: grey;
}

.OverlayButton.active {
  color: white;
  border-color: white;
}