.Gallery {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  margin: 5% 15% 10% 15%;
  grid-row-gap: 5%;
  grid-column-gap: 5%;
}

@media screen and (min-width: 320px) {
  .Gallery {
    margin: 5%;
    grid-row-gap: 0;
  }
}

.GalleryItem {
  text-align: center;
  text-decoration-line: none;
  opacity: 0.8;
}

.GalleryItem:hover {
  opacity: 1;
}

.GalleryItem p {
  margin-top: 10px;
  font-size: 20px;
}

.ThumbnailImage {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  border: 2px solid white;
  border-radius: 2px;
  opacity: 0.8;
}
