.BackButton {
  position: absolute;
  margin-left: 15px;
  margin-top: 3px;
  font-size: 2.4em;
}

.BackButton.active {
  color: white;
  border-color: white;
  cursor: pointer;
}

.BackButton.inactive {
  border-color: grey;
  color: grey;
  cursor: default;
}

.Header {
  line-height: var(--header-height);
  height: var(--header-height);
  border-bottom: white 1px solid;
}

.Navbar {
  line-height: inherit;
  float: right;
  vertical-align: middle;
  margin-right: 15px;
}

.Navbar a {
  text-decoration: none;
  padding-left: 10px;
}

.Navbar a:hover {
  text-decoration: underline;
  color: white;
}
